@import "../../style/variables.scss";

.char {
    &__content {
        margin-top: 50px;
        display: grid;
        grid-template-columns: 650px 425px;
        column-gap: 25px;
        align-items: start;
    }
    &__grid {
        display: grid;
        grid-template-columns: repeat(3, 200px);
        column-gap: 25px;
        row-gap: 30px;
    }
    &__item {
        width: 200px;
        height: 318px;
        background-color: $dark;
        box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.25);
        padding: 15px;
        cursor: pointer;
        transition: all 500ms ease;
        img {
            width: 200px;
            height: 200px;
            object-fit: cover;
            transform: translate(-15px, -15px);
        }
        &_selected {
            box-shadow: 0 5px 20px $main-color;
            transform: translateY(-8px);
        }
        &-enter {
            opacity: 0;
        }

        &-enter-active {
            opacity: 1;
        }
        &-exit {
            opacity: 1;
        }
        &-exit-active {
            opacity: 0;
        }
    }
    &__name {
        font-weight: bold;
        font-size: 22px;
        line-height: 29px;
        text-transform: uppercase;
        color: #fff;
    }
}

@media (max-width: 1200px) {
    .char {
        &__content {
            display: flex;
            flex-direction: column;
            flex-direction: column-reverse;
            margin: 0;
            & * {
                width: 100%;
            }
        }
        &__list {
            padding: 0 20px;
            margin-top: 50px;

            & button {
                margin-top: 20px;
            }
        }
        &__grid {
            display: grid;
            grid-template-columns: 1fr 1fr;
            row-gap: 30px;
        }
        &__item {
            width: 100%;
            height: 100%;
            padding: 0px 0px 20px 0px;

            align-self: center;
            margin: 0 auto;

            & img {
                width: 100%;
                height: 200px;
                object-fit: cover;
                transform: translate(0);
                margin-bottom: 10px;
            }
        }
        &__name {
            padding: 0 10px;
            font-weight: bold;
            font-size: 18px;
            line-height: 29px;
            text-transform: uppercase;
            color: #fff;
        }
    }
}
